@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form {
	background: #ffffff;
	max-width: 350px;
	margin: 0 auto;
	padding: 45px;
	text-align: center;
}
.form input {
	font-family: 'Roboto', sans-serif;
	outline: 0;
	background: #eee;
	width: 100%;
	border: 0;
	margin: 0 0 15px;
	padding: 15px;
	box-sizing: border-box;
	font-size: 14px;
}
.form button {
	font-family: 'Roboto', sans-serif;
	text-transform: uppercase;
	outline: 0;
	background: #222;
	width: 100%;
	border: 0;
	padding: 15px;
	color: #eee;
	font-size: 14px;
	transition: all 0.3 ease;
	cursor: pointer;
}
.form button:hover,
.form button:active,
.form button:focus {
	background: #53d769;
}
.form .message {
	margin: 15px 0 0;
	color: #b3b3b3;
	font-size: 12px;
}
.form .message a {
	color: #4caf50;
	text-decoration: none;
}
.form .register-form {
	display: none;
}
.container {
	position: relative;
	z-index: 1;
	max-width: 300px;
	margin: 0 auto;
}
body {
	background: #53d769; /* fallback for old browsers */
	background: linear-gradient(to left, #53d769, #fecb2e);
	font-family: 'Roboto', sans-serif;
}

#mainImage {
	object-fit: cover;
	height: 200px;
	background-color: #fff;
}

